.modal1 {
  background-color: #FFFFFF;
  box-shadow: 1px 0 6px #0000004C;
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  will-change: transform;
  position: fixed;
  z-index: 1000; }

.feedback-form__close-btn {
  background: none;
  border: none;
  margin: 0;
  padding: 24px 0 32px 16px;
  cursor: pointer;
  display: flex;
  align-items: center; }

.feedback-form__close-img {
  width: 40px;
  height: 40px;
  margin: 0;
  padding: 0; }

.feedback-form__title {
  padding-left: 16px;
  margin: 0 0 24px;
  position: relative; }

.feedback-form__title::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 4px;
  height: 40px;
  background-color: #41F6D7;
  border-radius: 2px; }

.feedback-form__form {
  padding: 0 16px;
  display: flex;
  flex-direction: column; }

.feedback-form__input,
.feedback-form__textarea {
  margin: 0 0 15px 0;
  border: 1px solid #EAEAEA;
  border-radius: 10px;
  color: #B5B6BC;
  background-color: #FFFFFF;
  height: 48px;
  padding: 12px 16px;
  box-sizing: border-box;
  font-size: 16px;
  outline: none; }

.feedback-form__textarea {
  height: 120px;
  resize: none; }

.feedback-form__policy {
  font-size: 14px;
  color: #7E7E82;
  margin: 0 0 20px 0;
  line-height: 1.5; }

.feedback-form__policy-link {
  color: #FF3E79;
  text-decoration: none; }

.feedback-form__submit-btn {
  border: none;
  cursor: pointer;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  background-color: transparent;
  margin: 0;
  padding: 0; }

@media (min-width: 768px) {
  .overlay-modal1 {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #0000004C;
    opacity: 0;
    visibility: hidden;
    transition: opacity 0.3s ease-in-out;
    z-index: 999; }
  .overlay-modal1.active {
    opacity: 1;
    visibility: visible; }
  .modal1 {
    background-color: #FFFFFF;
    box-shadow: 1px 0 6px #0000004C;
    display: flex;
    flex-direction: column;
    width: 432px;
    height: 100vh;
    transform: translateX(100%);
    transition: transform 0.3s ease-in-out;
    position: fixed;
    z-index: 1000;
    right: 0; } }
