.header {
  max-height: 88px;
  padding: 19px 16px;
  background-color: #FFFFFF;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #41F6D7; }

.header__left {
  display: flex;
  align-items: center;
  gap: 16px; }

.header__logo img {
  max-height: 50px;
  display: block; }

.header__nav {
  display: flex;
  align-items: center; }

.header__icons-service {
  display: flex;
  gap: 16px;
  list-style: none;
  padding: 0 18px 0 0;
  margin: 0;
  align-items: center; }

.header__icons-repair {
  display: flex;
  gap: 16px;
  list-style: none;
  padding: 0 0 0 18px;
  margin: 0;
  align-items: center;
  position: relative; }

.header__icons-repair::before {
  content: "";
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 2px;
  height: 32px;
  background-color: #EAEAEA; }

.header__icon {
  max-height: 40px;
  display: block; }

.header__icon-button {
  background: none;
  border: none;
  margin: 0;
  padding: 0;
  cursor: pointer;
  display: flex;
  align-items: center; }

.header__icon-text {
  display: none; }

.header__icon-button:hover {
  opacity: 0.7; }

.hidden {
  display: none; }

@media (min-width: 768px) {
  .header {
    max-height: 88px;
    padding: 19px 24px; }
  .header__logo {
    position: relative;
    padding-left: 16px; }
  .header__icon-text {
    display: none; }
  .logo__before::before {
    content: "";
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    width: 2px;
    height: 32px;
    background-color: #EAEAEA; }
  .hidden {
    display: flex; } }

@media (min-width: 1440px) {
  .header {
    border-bottom: none;
    background-color: #F8F8F8;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    position: relative;
    z-index: 1;
    margin-left: 320px;
    padding: 42px 32px 8px 0; }
  .header__left {
    display: none; }
  .header__icons-repair::before {
    display: none; }
  .header__logo::before {
    display: none; }
  .header__icons-repair .header__icon-text-li {
    display: flex;
    align-items: center; }
  .header__icon-text {
    margin-right: 10px;
    font-size: 16px;
    display: inline-block; }
  .hidden {
    display: none; } }
