.swiper-pagination {
  position: static;
  padding-top: 13px;
  padding-bottom: 13px; }

.swiper {
  width: auto;
  height: auto; }

.swiper-gradient {
  position: relative; }

.swiper-gradient:before {
  content: '';
  position: absolute;
  top: 97px;
  right: 0;
  width: 24px;
  height: 72px;
  pointer-events: none;
  background: linear-gradient(to right, #F8F8F8FF 0%, #F8F8F8FF 100%);
  z-index: 10; }

.swiper-slide {
  text-align: center;
  font-size: 16px;
  background: #FFFFFF;
  display: flex;
  align-items: center;
  flex-direction: row;
  border: 1px solid #EAEAEA;
  border-radius: 6px;
  justify-content: space-between;
  cursor: pointer;
  padding: 19px;
  margin: 0;
  width: 240px;
  height: 72px;
  box-sizing: border-box; }

.type-slide {
  width: 238px;
  height: 158px;
  align-items: start;
  justify-content: space-between;
  flex-direction: column;
  position: relative; }

.type-button {
  margin-left: auto; }

.swiper-wrapper {
  background-color: #F8F8F8;
  width: auto;
  height: auto; }

.swiper__title {
  display: flex;
  padding: 16px;
  font-weight: 400;
  color: #7E7E82;
  font-size: 16px;
  line-height: 24px;
  letter-spacing: -0.2px;
  text-align: left;
  text-transform: uppercase;
  width: 100%;
  max-height: 88px;
  justify-content: space-between;
  background: linear-gradient(to left, #FFFFFF, #F8F8F8);
  margin: 0 0 16px;
  border-top: 1px solid #41F6D7;
  box-sizing: border-box; }

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;
  background-color: #DDDDDD;
  opacity: 1;
  margin: 0 4px;
  border-radius: 50%; }

.swiper-pagination-bullet-active {
  background-color: #B5B6BC; }

.slide__company-button {
  width: 40px;
  height: 40px; }

.swiper__link-button {
  display: none; }

.service-card {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 260px;
  height: 200px;
  padding: 16px;
  text-align: left;
  box-sizing: border-box;
  position: relative; }

.service-card__content {
  flex-grow: 1; }

.service-card__title {
  font-size: 14px;
  color: #7E7E82;
  margin: 0; }

.service-card__description {
  font-size: 14px;
  color: #1B1C21;
  margin: 0 0 16px 0; }

.service-card__price {
  font-size: 14px;
  color: #7E7E82;
  margin: 0; }

.service-card__free {
  font-size: 14px;
  color: #1B1C21;
  margin: 0 0 16px 0; }

.service-card__label {
  font-size: 14px;
  color: #7E7E82;
  margin: 0; }

.service-card__value {
  font-size: 14px;
  color: #1B1C21;
  margin: 0; }

.service-card__button {
  cursor: pointer;
  width: 119px;
  height: 32px;
  background-color: transparent;
  border: none;
  position: absolute;
  top: 152px;
  left: 125px;
  margin: 0;
  padding: 0; }

.price-table__header {
  display: none;
  padding-top: 24px;
  color: #7E7E82; }

.after-swiper__description-text {
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 16px;
  color: #1B1C21; }

.after-swiper__link-button {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-left: 16px;
  margin-right: 16px;
  margin-bottom: 60px;
  padding: 0;
  background: none;
  border: none;
  cursor: pointer;
  font-size: inherit;
  font-family: "TT Lakes", sans-serif;
  color: #1B1C21;
  text-align: left;
  font-weight: 600;
  line-height: 24px; }

.after-swiper__link-button-arrow {
  position: relative;
  margin: 0; }

.after-swiper__link-image {
  position: absolute;
  width: 24px;
  height: 27px; }

.text-footer {
  color: #7E7E82;
  text-align: center;
  font-size: 14px; }

.copyright {
  margin: 0;
  padding-top: 16px; }

.text-footer__all {
  border-top: 1px solid #41F6D7; }

.Apesong {
  margin: 0;
  padding: 0; }

.politics {
  margin: 16px 0; }

.information {
  margin-bottom: 16px; }

@media (min-width: 768px) {
  .swiper {
    width: 768px;
    height: auto; }
  .swiper__title {
    width: 767px;
    padding-left: 24px;
    margin: 46px auto 0;
    border-radius: 6px; }
  .swiper-wrapper {
    display: flex;
    flex-wrap: wrap;
    gap: 16px 24px;
    margin: 24px; }
  .swiper-slide {
    width: calc(33.33% - 16px);
    box-sizing: border-box; }
  .swiper-pagination {
    display: none; }
  .swiper-gradient:before {
    display: none; }
  .swiper__link-button {
    display: flex;
    align-items: center;
    gap: 8px;
    background: none;
    border: none;
    color: #1B1C21;
    font-size: 16px;
    font-weight: 500;
    text-decoration: none;
    cursor: pointer;
    padding-left: 24px; }
  .swiper__link-image {
    width: 24px;
    height: 24px;
    display: block;
