    left: 585px;
    top: 16px; }
  .service-card__content {
    display: grid;
    grid-template-columns: 348px 96px 101px auto;
    width: 768px;
    margin-left: auto;
    margin-right: auto; }
  .header__title {
    padding-left: 40px;
    width: 372px; }
  .header__price {
    width: 96px; }
  .after-swiper__description-text {
    width: 65%;
    margin-left: 0;
    margin-top: 0; }
  .after-swiper__link-button {
    margin-left: 0; }
  .after-swiper__description {
    margin: 0 auto;
    width: 720px; }
  .text-footer__all {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0 auto 25px;
    width: 720px;
    padding-top: 32px; }
  .footer-left {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    flex: 1; }
  .text-footer {
    color: #7E7E82;
    margin: 0;
    text-align: center;
    font-size: 14px; }
  .copyright {
    margin: 0;
    padding: 0; }
  .politics {
    flex: 1;
    text-align: left; }
  .Apesong {
    text-align: left; }
  .information {
    text-align: right;
    flex: 1; } }

